.window-size-card {
    border: 1px solid #ccc;
    padding: 16px;
    border-radius: 8px;
    width: 300px;
    background-color: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .window-size-card h2 {
    font-size: 1.5em;
    margin-bottom: 8px;
    color: #333;
  }
  
  .window-size-card p {
    font-size: 1em;
    color: #666;
  }
  