.card {
    border: 1px solid #ccc;
    padding: 16px;
    border-radius: 8px;
    width: 300px;
    background-color: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .card-header {
    font-size: 1.5em;
    margin-bottom: 8px;
    color: #333;
  }
  
  .card-content {
    font-size: 1em;
    color: #666;
    margin-bottom: 16px;
  }
  
  .card-footer {
    text-align: center;
  }
  
  .card-footer button {
    background-color: #ccc;
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    cursor: pointer;
  }
  